@use "global" as *;

.info {
    font-size: 13px;
    font-weight: 300;
    line-height: calc(19 / 13);
}
.info__box {
    display: flex;
}
.info__img {
    width: 95px;
}
.info__meta {
    margin-left: 12px;
    width: calc(100% - 107px);
}
.info__brand {

}
.info__name {

}
.info__color {

}
.info__size {

}
.info__wrapping {

}
.info__order {
    
}
.info__explain {
    font-size: 12px;
    font-weight: 300;
    line-height: calc(17 / 12);
    color: #B94A48;
}
.info__note {
    font-size: 12px;
    font-weight: 300;
    letter-spacing: 0.05em;
    color: #B94A48;
    margin-top: 4px;
}

@media screen and (min-width: 695px) and (max-width: 899px) {
    .info__box {
        display: block;
    }
    .info__meta {
        width: 100%;
        margin-top: 5px;
    }
}

@media screen and (max-width: 694px) {
    .info {
        font-size: 12px;
        letter-spacing: 0.05em;
    }
    .info__brand {
        line-height: 1;
    }
    .info__name {
        margin-top: 7px;
    }
    .info__color {
        margin-top: 4px;
    }
    .info__size {
        margin-top: 5px;
    }
    .info__wrapping {
        margin-top: 5px;
    }
    .info__img {
        width: 100px;
    }
    .info__meta {
        margin-left: 10px;
        width: calc(100% - 110px);
    }
    .info__subtotal {
        margin-top: 4px;
    }
    .info__explain {
        margin-top: 7px;
    }
}