@use "global" as *;

.recommend-block {

}
.recommend-block__img {
    position: relative;
}
.recommend-block__icon {
    position: absolute;
    width: 48px;
    top: 0;
    right: 0;
    display: block;
}
.recommend-block__name {
    font-size: 13px;
    margin-top: 5px;
}
.recommend-block__kind {
    font-size: 15px;
    margin-top: 4px;
}
.recommend-block__price {
    font-size: 13px;
    margin-top: 7px;
}

@media screen and (max-width: 694px) {
    .recommend-block__icon {
        width: 24px;
    }
    .recommend-block__name {
        font-size: 12px;
        margin-top: 9px;
    }
    .recommend-block__kind {
        font-size: 12px;
    }
    .recommend-block__price {
        font-size: 12px;
        margin-top: 10px;
    }
}