@use "global" as *;

.hope {

}
.hope__date {
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 0.05em;
    margin-top: 30px;
}
.hope__time {
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 0.05em;
    margin-top: 7px;
}
.hope__box-wrapper {
    display: none;
}
.hope__box {
    display: flex;
    align-items: center;
    margin-top: 30px;
}
.hope__change {
    width: 380px;
    max-width: 100%;
    height: 50px;
    line-height: 50px;
    text-align: center;
    background-color: #fff;
    border: 1px solid #000;
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 0.15em;
    cursor: pointer;
    transition: opacity .3s ease 0s;
    margin-top: 16px;
}
.hope__change.is-none {
    display: none;
}
.hope__change:hover {
    opacity: 0.7;
}
.hope__select {
    display: flex;
    align-items: center;
    position: relative;
}
.hope__select:not(:first-child) {
    margin-left: 24px;
}
.hope__select::after {
    content: "";
    position: absolute;
    top: 4px;
    right: 4px;
    width: 24px;
    height: 24px;
    background: url(../images/common/select-arrow.png) no-repeat center center / contain;
    z-index: -1;
}
.hope__select label {
    font-size: 13px;
    font-weight: 300;
    margin-right: 12px;
}
.hope__select select {
    width: 190px;
    max-width: 100%;
    height: 32px;
    border: 1px solid #707070;
    background-color: transparent;
    appearance: none;
    outline: none;
    box-shadow: none;
    font-size: 12px;
    font-weight: 300;
    color: #878787;
    padding-left: 8px;
}
.hope__explain {
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 0.05em;
    line-height: calc(23 / 13);
    color: #B94A48;
    margin-top: 15px;
}
.hope__explain a {
    display: inline-block;
    text-decoration: underline;
}
.hope__specify {
    margin-top: 5px;
}
.hope__specify a {
    display: inline-block;
    text-decoration: underline;
    font-size: 13px;
    font-weight: 300;
}
.hope__register {
    margin-top: 30px;
}
.hope__register button {
    width: 380px;
    max-width: 100%;
    height: 50px;
    background-color: #fff;
    border: 1px solid #000;
    text-align: center;
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 0.15em;
}
.hope__not {
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 0.05em;
    color: #B94A48;
}

@media screen and (min-width: 695px) and (max-width: 1149px) {
    .hope__box {
        display: block;
    }
    .hope__select:not(:first-child) {
        margin-left: 0;
        margin-top: 12px;
    }
    .hope__select label {
        margin-right: 0;
        width: 70px;
    }
    .hope__select select {
        width: calc(100% - 70px);
    }
}

@media screen and (max-width: 694px) {
    .hope__date {
        font-size: 12px;
        margin-top: 0;
    }
    .hope__time {
        font-size: 12px;
        margin-top: 5px;
    }
    .hope__box {
        display: block;
    }
    .hope__select {
        display: block;
    }
    .hope__select select {
        width: 100%;
        height: 40px;
        margin-top: 5px;
        font-size: 13px;
    }
    .hope__select label {
        display: block;
        margin-right: 0;
    }
    .hope__select:not(:first-child) {
        margin-left: 0;
        margin-top: 17px;
    }
    .hope__select::after {
        width: 20px;
        height: 20px;
        top: 35px;
        right: 5px;
    }
    .hope__explain {
        font-size: 12px;
        line-height: calc(35 / 24);
        letter-spacing: 0.05em;
        margin-top: 8px;
    }
    .hope__specify a {
        font-weight: 600;
        font-size: 12px;
    }
    .hope__register {
        margin-top: 18px;
        text-align: center;
    }
    .hope__register button {
        font-size: 15px;
        letter-spacing: 0;
    }
    .hope__change {
        margin-left: auto;
        margin-right: auto;
    }
}