@use "global" as *;

.register {

}
.register__title {
    font-size: 18px;
    font-weight: 600;
    text-align: center;
}
.register__step {
    margin-top: 45px;
}
.register__explain {
    font-size: 12px;
    font-weight: 300;
    letter-spacing: 0.05em;
    text-align: center;
    margin-top: 45px;
}
.register__explain--pc {
    font-size: 12px;
    font-family: 'Noto Sans JP', sans-serif;
    font-weight: 500;
    line-height: calc(17 / 12);
    letter-spacing: 0.05em;
    margin-top: 48px;
    text-align: center;
}
.register__explain--sp {
    display: none;
}
.register__explain--sp01 {
    display: none;
}
.register__terms {
    margin-top: 68px;
}
.register__check {
    margin-top: 28px;
    text-align: center;
}
.register__check label {
    display: flex;
    align-items: center;
    justify-content: center;
}
.register__check input {
    display: inline-block;
    width: 14px;
    height: 14px;
    border: 1px solid #000;
    margin: 0;
    padding: 0;
    border-radius: 4px;
}
.register__check input:checked {
    background-color: #000;
}
.register__check span {
    font-size: 13px;
    font-weight: 300;
    margin-left: 6px;
}
.register__next-btn {
    margin-top: 46px;
    text-align: center;
}
.register__next-btn button {
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 0.15em;
    width: 380px;
    max-width: 100%;
    height: 50px;
    border: 1px solid #000;
    line-height: 50px;
    transition: opacity .3s ease 0s;
}
.register__next-btn button:hover {
    opacity: 0.8;
}
.register__note {
    font-size: 11px;
    font-weight: 300;
    margin-top: 5px;
    padding-left: 4px;
    color: #878787;
}
.register__instructions {
    font-size: 12px;
    font-weight: 300;
    letter-spacing: 0.05em;
    line-height: calc(17 / 12);
    margin-top: 14px;
}
.register__mail {
    margin-top: 52px;
}
.register__submit {
    margin-top: 50px;
    text-align: center;
}
.register__submit input {
    width: 380px;
    max-width: 100%;
    height: 50px;
    line-height: 50px;
    background-color: #000;
    color: #fff;
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 0.15em;
    transition: opacity .3s ease 0s;
}
.register__submit input:hover {
    opacity: 0.8;
}
.register__complete {
    text-align: center;
    margin-top: 49px;
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 0.15em;
}
.register__text {
    font-size: 12px;
    font-weight: 300;
    letter-spacing: 0.05em;
    line-height: calc(17 / 12);
    margin-top: 15px;
    text-align: center;
}
.register__text span {
    font-weight: 600;
    color: #BB4F4D;
    display: block;
}
.register__top-link {
    display: block;
    width: 380px;
    max-width: 100%;
    height: 50px;
    line-height: 50px;
    margin-top: 50px;
    background-color: #CDCDCD;
    text-align: center;
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 0.15em;
    margin-left: auto;
    margin-right: auto;
}
.register__use {
    font-size: 12px;
    font-weight: 300;
    line-height: calc(17 / 12);
    letter-spacing: 0.05em;
    text-align: center;
    margin-top: 46px;
}
.register__use a {
    display: block;
    font-weight: 600;
    text-decoration: underline;
}
.register__mandatory {
    font-size: 12px;
    font-weight: 300;
    letter-spacing: 0.05em;
    color: #B94A48;
    text-align: center;
    margin-top: 44px;
}
.register__dl {

}
.register__box:not(:first-child) {
    margin-top: 26px;
}
.register__box--flex {
    display: flex;
}
.register__body {

}
.register__row {
    display: flex;
    align-items: center;
}
.register__row--start {
    align-items: flex-start;
}
.register__row--end {
    align-items: flex-end;
}
.register__row--mt {
    margin-top: 25px;
}
.register__dl {
    margin-top: 39px;
}
.register__dt {
    font-size: 13px;
    font-weight: 600;
    width: 180px;
    max-width: 100%;
}
.register__dt span {
    color: #B94A48;
}

.register__dd {
    font-size: 12px;
    font-weight: 300;
    letter-spacing: 0.05em;
    margin-left: 20px;
}
.register__dd [type=email] {
    display: block;
    width: 380px;
    max-width: 100%;
    height: 32px;
    padding: 10px 8px;
    font-size: 12px;
    font-weight: 300;
    appearance: none;
    box-shadow: none;
    outline: none;
    border: 1px solid #707070;
}
.register__dd-check {
    margin-top: 16px;
    display: flex;
}
.register__dd-check label {
   display: flex;
   align-items: center;
}
.register__dd-check label:not(:first-child) {
    margin-left: 21px;
}
.register__dd-check span {
    display: inline-block;
    margin-left: 6px;
}
   
.register__dd-check input {
    width: 13px;
    height: 13px;
    border: 1px solid #000;
    border-radius: 3px;
   display: block;
   margin: 0;
}
.register__dd-check input:checked {
    background-color: #000;
}
.register__dd-explain {
    font-size: 11px;
    font-weight: 300;
    color: #878787;
    margin-top: 5px;
}
.register__dd-name-wrapper {
    display: flex;
}
.register__dd-name {
    /*
    display: flex;
    align-items: center;
    */
    position: relative;
}
.err-msg-id {
    margin-top: 6px;
}
.err-msg-pin {
    margin-top: 6px;
}
.err-msg-name {
    margin-top: 6px;
}
.err-msg-last-name {
    margin-top: 6px;
}
.err-msg-first-name {
    margin-top: 6px;
}
.err-msg-furigana-last-name {
    margin-top: 6px;
}
.err-msg-furigana-first-name {
    margin-top: 6px;
}
.err-msg-address {
    margin-top: 6px;
}
.err-msg-apartment {
    margin-top: 6px;
}
.err-msg-tel {
    margin-top: 6px;
}
.err-msg-pass {
    margin-top: 6px;
}
.err-msg-passre {
    margin-top: 6px;
}
.err-msg-address1 {
    margin-top: 6px;
}
.err-msg-address2 {
    margin-top: 6px;
}
.form-invalid {
    font-size: 11px;
    font-weight: 300;
    color: #C46866;
}
.input-invalid {
    background-color: #FFEAEA !important;
    border: 1px solid #B94A48 !important;
}
.register__dd-name:not(:first-child) {
    margin-left: 40px;
}
.register__dd-name label {
    font-size: 13px;
    font-weight: 300;
    position: absolute;
    top: 6.5px;
    left: -18px;
}
.register__dd-name input {
    width: 190px;
    height: 32px;
    appearance: none;
    outline: none;
    box-shadow: none;
    background-color: #fff;
    border: 1px solid #707070;
    padding: 10px 8px;
    margin-left: 7px;
}
.register__dd-name input::placeholder {
    font-size: 12px;
    font-weight: 300;
    color: #878787;
}
.register__dd-address-wrapper {
    display: flex;
}
.register__dd-address {
    padding-left: 25px;
    position: relative;
}
.register__dd-address:first-child::before {
    content: "〒";
    position: absolute;
    top: 7px;
    left: 0;
    font-size: 13px;
    font-weight: 300;
}
.register__dd-address:nth-child(2)::before {
    content: "ー";
    position: absolute;
    top: 7px;
    left: 0;
    font-size: 13px;
    font-weight: 300;
}
.register__dd-address:nth-child(2) {
    margin-left: 12px;
    
}
.register__dd-address [type=text] {
    appearance: none;
    box-shadow: none;
    outline: none;
    background-color: #fff;
    border: 1px solid #707070;
    width: 190px;
    height: 32px;
    padding: 10px 8px;
}
.register__dd-address [type=submit] {
    appearance: none;
    box-shadow: none;
    outline: none;
    background-color: #CDCDCD;
    border: none;
    width: 190px;
    height: 32px;
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 0.15em;
    padding: 7px 21px 5px 21px;
}
.is-red {
    color: #B94A48;
}
.register__completed {
    margin-top: 43px;
    text-align: center;
}
