.register__radio {
    display: flex;
}
.register__radio label {
    display: flex;
    align-items: center;
}
.register__radio span {
    margin-right: 24px;
    margin-left: 6px;
}
.register__radio input {
    width: 13px;
    height: 13px;
    border-radius: 3px;
    border: 1px solid #000;
    display: block;
    margin: 0;
}
.register__radio input:checked {
    background-color: #000;
}
.register__select select {
    width: 190px;
    height: 32px;
    border: 1px solid #878787;
    padding: 7px 8px;
    position: relative;
    z-index: 1;
}
.is-select {
    color: #878787 !important;
}
.register__select-wrapper {
    display: flex;
}
.register__select {
    display: flex;
    align-items: center;
    position: relative;
}
.register__select::after {
    content: "";
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 27px;
    width: 24px;
    height: 24px;
    background: url(../images/common/select-arrow.png) no-repeat center center / cover;
}
.register__select--post::after {
    right: 4px;
}
.register__select:not(:first-child) {
    margin-left: 40px;
}
.register__select span {
    font-size: 13px;
    font-weight: 300;
    display: inline-block;
    margin-left: 12px;
}
.register__confirm {
