@use "global" as *;

.point {

}
.point__enable {
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 0.05em;
}
.point__check {
    margin-top: 14px;
}
.point__check label {
    display: flex;
    align-items: center;
}
.point__check span {
    margin-left: 6px;
    font-size: 13px;
    font-weight: 300;
}
.point__box {
    display: flex;
    align-items: center;
    margin-top: 16px;
}
.point__point {
    width: 146px;
    max-width: 100%;
}
.point__point [type=text] {
    width: 100%;
    height: 32px;
    background-color: #fff;
    border: 1px solid #707070;
    padding-left: 8px;
    font-size: 12px;
    font-weight: 300;
}
.point__register {
    width: 104px;
    max-width: 100%;
    margin-left: 10px;
}
.point__register button {
    width: 100%;
    height: 32px;
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 0.15em;
    color: #fff;
    text-align: center;
    background-color: #000;
}
.point__all {
    width: 200px;
    max-width: 100%;
    margin-left: 10px;
}
.point__all button {
    width: 100%;
    height: 32px;
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 0.15em;
    color: #fff;
    text-align: center;
    background-color: #000;
}

@media screen and (min-width: 695px) and (max-width: 801px) {
    .point__all button {
        font-size: 12px;
        letter-spacing: 0;
    }
}

@media screen and (max-width: 694px)  {
    .point__check {
        margin-top: 16px;
    }
    .point__box {
        flex-wrap: wrap;
        margin-top: 10px;
        justify-content: space-between;
    }
    .point__point {
        width: calc((262 / 347) * 100%);
    }
    .point__point [type=text] {
        height: 40px;
    }
    .point__register {
        width: calc((75 / 347) * 100%);
        margin-left: 0;
    }
    .point__register button {
        height: 40px;
        font-size: 11px;
        letter-spacing: 0;
    }
    .point__all {
        width: 100%;
        margin-left: 0;
        margin-top: 10px;
    }
    .point__all button {
        height: 50px;
        line-height: 50px;
        font-size: 15px;
        letter-spacing: 0;
    }
    .point__enable {
        display: flex;
    }
    .point__enable span {
        display: inline-block;
    }
    .point__enable span:nth-child(2) {
        margin-left: auto;
    }
}