@use "global" as *;

.specification {

}
.specification__box {
    display: flex;
}
.specification__check:not(:first-child) {
    margin-left: 24px;
}
.specification__check label {
    display: flex;
    align-items: center;
}
.specification__check span {
    font-size: 13px;
    font-weight: 300;
    margin-left: 6px;
}

@media screen and (max-width: 694px) {
    .specification__check:not(:first-child) {
        margin-left: 10px;
    }
}