@use "global" as *;

.inner {
  width: 1380px;
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  height: inherit;
  padding-left: 70px;
  padding-right: 70px;
}

@media screen and (min-width: 751px) and (max-width: 1240px) {
  .inner {
    width: calc(100% - 140px);
    padding-left: 0;
    padding-right: 0;
  }
}

@media screen and (max-width: 694px) {
  .inner {
    width: 100%;
    padding-left: 14px;
    padding-right: 14px;
  }
}
