@use "global" as *;

.input {
    width: 400px;
    max-width: 100%;
    height: 32px;
    background-color: #fff;
    border: 1px solid #707070;
    font-size: 12px;
    font-weight: 300;
    padding-left: 8px;
}
.input::placeholder {
    font-size: 12px;
    font-weight: 300;
    color: #878787;
}


@media screen and (max-width: 694px) {
    .input {
        width: 100%;
        height: 40px;
        font-size: 13px;
    }
}