@use "global" as *;

.check label {
    display: flex;
    align-items: center;
}
.check [type=radio] {
    display: inline-block;
    width: 13px;
    height: 13px;
    border-radius: 3px;
    margin: 0;
    padding: 0;
    appearance: none;
    box-shadow: none;
    border: 1px solid #000;
    outline: none;
}
.check [type=radio]:checked {
    background-color: #000;
}

.check [type=checkbox] {
    display: inline-block;
    width: 13px;
    height: 13px;
    border-radius: 3px;
    margin: 0;
    padding: 0;
    appearance: none;
    box-shadow: none;
    border: 1px solid #000;
    outline: none;
}
.check [type=checkbox]:checked {
    background-color: #000;
}

@media screen and (max-width: 694px) {
    .check [type=radio] {
        width: 15px;
        height: 15px;
    }
}