@use "global" as *;

.addresss {

}
.addresss__dl {

}
.addresss__row {

}
.addresss__row:not(:first-child) {
    margin-top: 53px;
}
.addresss__dt {
    font-size: 13px;
    font-weight: 600;
    letter-spacing: 0.05em;
    padding-left: 10px;
}
.addresss__dd {
    margin-top: 12px;
    padding-top: 11px;
    border-top: 1px solid #DFDFDF;
    padding-left: 10px;
}
.addresss__name {
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 0.05em;
}
.addresss__address {
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 0.05em;
    margin-top: 4px;
}
.addresss__tel {
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 0.05em;
    margin-top: 4px;
}
.addresss__delivery {
    margin-top: 6px;
    margin-bottom: 8px;
}
.addresss__delivery label {
    display: flex;
    align-items: center;
}
.addresss__delivery [type=radio] {
    display: inline-block;
    width: 13px;
    height: 13px;
    border-radius: 3px;
    margin: 0;
    padding: 0;
    appearance: none;
    box-shadow: none;
    border: 1px solid #000;
    outline: none;
}
.addresss__delivery [type=radio]:checked {
    background-color: #000;
}
.addresss__delivery span {
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 0;
    margin-left: 6px;
    display: inline-block;
}
.address__others {
    font-size: 13px;
    font-weight: 600;
    letter-spacing: 0.05em;
    padding-top: 17px;
    margin-top: 16px;
    border-top: 1px solid #DFDFDF;
    margin-left: -10px;
}
.address__others span {
    padding-left: 10px;
}
.address__other-address {
    margin-top: 9px;
}
.address__other-address:nth-child(n + 4) {
    display: none;
}
