.address__other-block {
    margin-left: 6px;
    position: relative;
    width: calc(100% - 19px);
}
.address__other-gift .address__other-block::before {
    content: "";
    position: absolute;
    width: 11px;
    height: 10px;
    background: url(../images/common/check.png) no-repeat center center / contain;
    top: 50%;
    transform: translateY(-50%);
    left: -18px;
}
/*
.address__other-gift [type=checkbox]:checked + .address__other-gift .address__other-block::before {
    display: block;
}
*/
.address__other-block .addresss__name {
    font-size: 12px;
    letter-spacing: 0;
}
.address__other-block .addresss__address {
    font-size: 12px;
    letter-spacing: 0;
    margin-top: 0;
}
.address__other-block .addresss__tel {
    font-size: 12px;
    letter-spacing: 0;
    margin-top: 0;
}
.address__more {
    margin-top: 12px;
}
