.address__new {
    margin-top: 20px;
    display: none;
}
.addresss__dd-gift {

}
.addresss__dd-gift label {
    align-items: center;
    display: flex;
}
.addresss__dd-gift [type=radio] {
    border-radius: 3px;
    background-color: #fff;
    border: 1px solid #000;
    width: 13px;
    height: 13px;
    display: block;
    margin: 0;
}
.addresss__dd-gift [type=radio]:checked {
    background-color: #000;
}
.addresss__dd-gift span {
    font-size: 13px;
    font-weight: 300;
    margin-left: 6px;
}
.addresss__dd-gift:not(:first-child) {
    margin-top: 14px;
}
.addresss__dd-gift-yes {
    padding-left: 20px;
    margin-top: 20px;
    display: flex;
    align-items: center;
}
.addresss__dd-gift-wrap:not(:first-child) {
    margin-left: 24px;
}
.addresss__dd-gift-wrap label {
    display: flex;
    align-items: center;
}
.addresss__dd-gift-wrap span {
    font-size: 13px;
    font-weight: 300;
    margin-left: 6px;
    width: calc(100% - 19px);
}
.addresss__dd-gift-wrap [type=radio]:checked {
    background-color: #000;
}
.address__other-gift-wrapper {
    padding-left: 24px;
    margin-top: 15px;
}
.address__other-gift {
    border-top: 1px solid #DFDFDF;
    padding-top: 10px;
    padding-bottom: 10px;
}
.address__other-gift:last-child {
    border-bottom: 1px solid #DFDFDF;
}
/*
.address__other-gift:not(:first-child) {
    margin-top: 10px;
}
*/
.address__other-gift:nth-child(n + 4)  {
    display: none;
}
.address__new-more {
    border: 1px solid #000 !important;
    font-family: $base-font-family;
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 0.15em;
    margin-top: 23px;
}
.address__new-more.is-none {
    display: none;
}


@media screen and (max-width: 694px) {
    .addresss__dt {
	    width: 100vw;
        margin-left: calc(50% - 50vw);
        margin-right: calc(50% - 50vw);
        padding-left: calc(50vw - 50%);
        padding-right: calc(50vw - 50%);
        padding-top: 8px;
        padding-bottom: 8px;
        background-color: #F0F0F0;
        font-size: 14px;
        font-weight: 300;
        letter-spacing: 0;
    }
    .addresss__dd {
        border-top: none;
        padding-left: 0;
        margin-top: 0;
        padding-top: 9px;
    }
    .addresss__name {
        font-size: 12px;
    }
    .addresss__name div {
        display: flex;
        align-items: center;
    }
    .addresss__name span {

    }
    .addresss__name span:first-child {
        width: 137.5px;
    }
    .addresss__name:nth-child(2) {
        width: calc(100% - 137.5px);
    }
    .addresss__address {
        font-size: 12px;
        line-height: calc(35 / 24);
        margin-top: 7px;
        letter-spacing: 0;
    }
    .addresss__address div {
        display: flex;
        align-items: center;
    }
    .addresss__address span {

    }
    .addresss__address span:first-child {
        width: 137.5px;
    }
    .addresss__address span:nth-child(2) {
        width: calc(100% - 137.5px);
    }
    .addresss__tel {
        font-size: 12px;
        letter-spacing: 0;
        margin-top: 5px;
    }
    .addresss__tel div {
        display: flex;
        align-items: center;
    }
    .addresss__tel span {
        
    }
    .addresss__tel span:first-child {
        width: 137.5px;
    }
    .addresss__tel span:nth-child(2) {
        width: calc(100% - 137.5px);
    }
    .addresss__row:not(:first-child) {
        margin-top: 23px;
    }
    .addresss__delivery {
        margin-top: 2px;
        margin-bottom: 14px;
    }
    .addresss__delivery [type=radio] {
        width: 15px;
        height: 15px;
    }
    .address__others {
        font-size: 14px;
        font-weight: 300;
        letter-spacing: 0;
        margin-left: 0;
    }
    .address__others span {
        padding-left: 0;
    }
    .address__other-block .addresss__name {
        font-size: 13px;
    }
    .address__other-block .addresss__address {
        font-size: 13px;
    }
    .address__other-block .addresss__tel {
        font-size: 13px;
    }
    .address__other-block {
        width: calc(100% - 21px);
    }
    .address__other-address {
        margin-top: 11px;
    }
    .address__more {
        margin-top: 16px;
        margin-left: auto;
        margin-right: auto;
    }
    .address__new-more {
        margin-top: 20px;
        margin-left: auto;
        margin-right: auto;
    }
    .addresss__dd-gift [type=radio] {
        width: 15px;
        height: 15px;
    }
    .addresss__dd-gift:not(:first-child) {
        margin-top: 17px;
    }
    .addresss__dd-gift-yes {
        padding-left: 0;
        display: block;
        border-top: 1px solid #D9D9D9;
        padding-top: 20px;
    }
    .addresss__dd-gift-wrap:not(:first-child) {
        margin-left: 0;
        margin-top: 17px;
    }
    .address__other-gift-wrapper {
        padding-left: 20px;
        margin-top: 10px;
    }
    .address__other-gift {
        border-top: none;
    }
    .address__other-gift:last-child {
        border-bottom: none;
    }
}