@use "global" as *;

.view-more {
    text-align: center;
    width: 380px;
    max-width: 100%;
    height: 50px;
    line-height: 50px;
    text-align: center;
    background-color: #fff;
    border: none;
    padding-right: 28px;
    cursor: pointer;
    transition: opacity .3s ease 0s;
    font-size: 14px;
    letter-spacing: 0.1em;
    font-family: 'Avenir',sans-serif;
}
.view-more:hover {
    opacity: 0.7;
}
.view-more span {
    position: relative;
}
.view-more .more.on-click {
    display: none;
}
.view-more .close.on-click {
    display: none;
}
.view-more span::after {
    content: "";
    position: absolute;
    width: 16px;
    height: 16px;
    background: url(../images/common/plus.png) no-repeat center center / contain;
    right: -30px;
    top: 50%;
    transform: translateY(-50%);
}

@media screen and (max-width: 694px) {
    .view-more {
        font-size: 15px;
        padding-right: 26px;
    }
    .view-more span::after {
        right: -24px;
    }
}