@use "global" as *;

.product {
    padding-top: 15px;
    padding-bottom: 15px;
}
.product__info {
    width: calc((376 / 1240) * 100%);
}
.product__price {
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 0.05em;
    width: calc((186 / 1240) * 100%);
    margin-left: 46px;
}
.product__order {
    width: calc((376 / 1240) * 100%);
    margin-left: 23px;
}
.product__select {
    display: inline-block;
    position: relative;
}
.product__select::after {
    content: "";
    position: absolute;
    width: 24px;
    height: 24px;
    background: url(../images/common/select-arrow.png) no-repeat center center / contain;
    top: 4px;
    right: 4px;
    z-index: -1;
}
.product__order select {
    width: 190px;
    height: 32px;
    appearance: none;
    box-shadow: none;
    outline: none;
    border: 1px solid #707070;
    background-color: transparent;
    font-size: 12px;
    font-weight: 300;
    color: #878787;
    padding-left: 7px;
}
.product__delete {
    margin-top: 8px;
}
.product__delete a {
    display: inline-block;
    text-decoration: underline;
    font-size: 13px;
    font-weight: 300;
    color: #707070;
}
.product__subtotal {
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 0.05em;
    width: calc((186 / 1240) * 100%);
    margin-left: 42px;
}

@media screen and (max-width: 694px) {
    .product {
        padding-top: 20px;
        padding-bottom: 20px;
    }
    .product:first-child {
        padding-top: 25px;
    }
    .product__info {
        width: 100%;
    }
    .product__select {
        display: flex;
        align-items: center;
    }
    .product__select span {
        font-size: 12px;
        font-weight: 300;
        letter-spacing: 0.05em;
    }
    .product__order select {
        width: 125px;
        height: 40px;
        margin-left: 4px;
        padding-left: 9px;
    }
    .product__order {
        margin-left: 0;
        display: flex;
        align-items: center;
        width: 100%;
        margin-top: 17px;
    }
    .product__delete a {
        font-size: 12px;
    }
    .product__delete {
        margin-top: 0;
        margin-left: auto;
    }
    .product__select::after {
        width: 20px;
        height: 20px;
        top: 11px;
    }
}