@use "global" as *;


.credit {

}
.credit__check {

}
.credit__check label {
    display: flex;
    align-items: center;
}
.credit__check span {
    font-size: 13px;
    font-weight: 300;
    margin-left: 6px;
}
.credit__kinds {
    margin-top: 12px;
}
.credit__kinds.is-none {
    display: none;
}
.credit__kind {
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 0.05em;
}
.credit__kind:not(:first-child) {
    margin-top: 4px;
}
.credit__change {
    width: 380px;
    max-width: 100%;
    line-height: 50px;
    height: 50px;
    border: 1px solid #000;
    background-color: #fff;
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 0.15em;
    text-align: center;
    margin-top: 20px;
    cursor: pointer;
    transition: opacity .3s ease 0s;
}
.credit__change.is-none {
    display: none;
}
.credit__change:hover {
    opacity: 0.7;
}
.credit__delivery {
    margin-top: 30px;
}
.credit__delivery label {
    display: flex;
    align-items: center;
}
.credit__delivery span {
    font-size: 13px;
    font-weight: 300;
    margin-left: 6px;
}
.credit__register {
    display: none;
}
.credit__img {

}
.credit__dl {
    margin-top: 30px;
}
.credit__row {
    display: flex;
    align-items: center;
}
.credit__row:not(:first-child) {
    margin-top: 30px;
}
.credit__dt {
    width: 180px;
    max-width: 100%;
    font-size: 13px;
    font-weight: 600;
}
.credit__dd {

}

.credit__dd [type=text] {
    width: 400px;
    max-width: 100%;
    height: 32px;
    background-color: #fff;
    border: 1px solid #707070;
    padding-left: 8px;
    font-size: 12px;
    font-weight: 300;
}
.credit__dd [type=text]::placeholder {
    color: #878787;
}
.credit__dd-explain {
    font-size: 11px;
    font-weight: 300;
    margin-top: 5px;
    color: #878787;
}
.credit__about {
    text-decoration: underline;
    font-size: 13px;
    font-weight: 300;
    margin-top: 6px;
    display: block;
}
.credit__img {
    width: 213px;
    margin-top: 15px;
}
.credit__dd-box {
    display: flex;
}
.credit__dd-select {
    display: flex;
    align-items: center;
    position: relative;
}
.credit__dd-select:not(:first-child) {
    margin-left: 30px;
}
.credit__dd-select::after {
    content: "";
    position: absolute;
    top: 4px;
    right: 27px;
    width: 24px;
    height: 24px;
    background: url(../images/common/select-arrow.png) no-repeat center center / contain;
    z-index: -1;
}
.credit__dd-select select {
    width: 95px;
    height: 32px;
    background-color: transparent;
    border: 1px solid #707070;
    padding-left: 8px;
    font-size: 12px;
    font-weight: 300;
}
.credit__dd-select span {
    font-size: 13px;
    font-weight: 300;
    margin-left: 12px;
}
.credit__box {
    display: flex;
    align-items: center;
}
.credit__info:not(:first-child) {
    margin-left: 24px;
}
.credit__info label {
    display: flex;
    align-items: center;
}
.credit__info [type=redio] {

}
.credit__info span {
    font-size: 13px;
    font-weight: 300;
    margin-left: 6px;
}
.credit__note {
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 0.05em;
    line-height: calc(23 / 13);
    margin-top: 15px;
    color: #878787;
    width: 400px;
    max-width: 100%;
}
.credit__dd-payment {
    position: relative;
    width: 380px;
    max-width: 100%;
}
.credit__dd-payment::after {
    content: "";
    position: absolute;
    top: 4px;
    right: 4px;
    width: 24px;
    height: 24px;
    background: url(../images/common/select-arrow.png) no-repeat center center / contain;
    z-index: -1;
}
.credit__dd-payment select {
    width: 380px;
    max-width: 100%;
    height: 32px;
    background-color: transparent;
    border: 1px solid #707070;
    appearance: none;
    outline: none;
    box-shadow: none;
    font-size: 12px;
    font-weight: 300;
    padding-left: 8px;
    color: #878787;
}
.credit__button {
    margin-top: 30px;
}
.credit__button button {
    width: 380px;
    max-width: 100%;
    height: 50px;
    line-height: 50px;
    text-align: center;
    background-color: #fff;
    border: 1px solid #000;
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 0.15em;
}

@media screen and (min-width: 695px) and (max-width: 1149px) {
    .credit__row {
        display: block;
    }
    .credit__dd {
        margin-top: 3px;
    }
    .credit__dd-payment select {
        width: 100%;
    }
}

@media screen and (max-width: 694px) {
    .credit__kind {
        font-size: 12px;
    }
    .credit__delivery {
        padding-top: 20px;
        margin-top: 20px;
        border-top: 1px solid #D9D9D9;
    }
    .credit__dl {
        margin-top: 18px;
    }
    .credit__row {
        display: block;
    }
    .credit__row:not(:first-child) {
        margin-top: 18px;
    }
    .credit__dt {
        font-weight: 300;
        font-size: 14px;
    }
    .credit__dd {
        margin-top: 5px;
    }
    .credit__dd [type=text] {
        height: 40px;
        font-size: 13px;
    }
    .credit__dd--flex {
        display: flex;
        align-items: center;
    }
    .credit__dd--flex [type=text] {
        width: calc(51% - 10px / 2);
    }
    .credit__dd--flex .credit__about {
        font-size: 12px;
        font-weight: 600;
        margin-left: 10px;
        width: calc(49% - 10px / 2);
        margin-top: 0;
    }
    .credit__dd-select select {
        width: 90px;
        height: 40px;
    }
    .credit__dd-select span {
        font-size: 14px;
        margin-left: 5px;
    }
    .credit__dd-select:not(:first-child) {
        margin-left: 10px;
    }
    .credit__dd-payment select {
        height: 40px;
        font-size: 13px;
        max-width: 100%;
    }
    .credit__note {
        font-size: 12px;
        line-height: calc(35 / 24);
        margin-top: 18px;
    }
    .credit__dd-select::after {
        width: 20px;
        height: 20px;
        top: 9px;
        right: 23px;
    }
    .credit__about {
        font-weight: 600;
        margin-top: 12px;
    }
    .credit__dd-payment::after {
        width: 20px;
        height: 20px;
        top: 11px;
    }
    .credit__button {
        text-align: center;
    }
    .credit__button button {
        font-size: 15px;
        letter-spacing: 0;
    }
    .credit__change {
        margin-left: auto;
        margin-right: auto;
        font-size: 15px;
        letter-spacing: 0;
    }
}