@use "global" as *;

.return-link {
    font-size: 15px;
    font-weight: 300;
    letter-spacing: 0;
    color: #000;
    size: 380px;
    max-width: 100%;
    height: 50px;
    line-height: 50px;
    background-color: #CDCDCD;
    display: block;
    text-align: center;
}
.return-link--black {
    color: #fff;
    background-color: #000;
}
@media screen and (max-width: 694px) {
    .return-link {
        margin-left: auto;
        margin-right: auto;
    }
}