@use "global" as *;

.coupon {

}
.coupon__box {
    display: flex;
    align-items: center;
}
.coupon__code {
    width: 240px;
}
.coupon__code [type=text] {
    width: 100%;
    height: 32px;
    background-color: #fff;
    border: 1px solid #707070;
    font-size: 12px;
    font-weight: 300;
    appearance: none;
    box-shadow: none;
    outline: none;
    padding-left: 8px;
}
.coupon__register {
    width: 104px;
    margin-left: 10px;
}
.coupon__register button {
    width: 100%;
    height: 32px;
    background-color: #000;
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 0.15em;
    text-align: center;
    color: #fff;
}
.coupon__explain {
    font-size: 12px;
    font-weight: 300;
    letter-spacing: 0.05em;
    line-height: calc(35 / 24);
}

@media screen and (max-width: 694px) {
    .coupon__box {
        margin-top: 12px;
    }
    .coupon__code {
        width: 262px;
    }
    .coupon__code [type=text] {
        height: 40px;
        font-size: 13px;
    }
    .coupon__register {
        width: 75px;
    }
    .coupon__register button {
        height: 40px;
        font-size: 11px;
        letter-spacing: 0;
    }
}