@use "global" as *;

.payment {

}
.payment__content {

}
.payment__content {
    /*
    border: 1px solid #DFDFDF;
    */
}
.payment__dl {
    margin: 0;
}
.payment__row {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.payment__row:not(:first-child) {
    margin-top: 9px;
}
.payment__dt {
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 0.05em;
}
.payment__dd {
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 0.05em;
}
.payment__box {
    padding: 26px 40px 25px;
    border-left: 1px solid #DFDFDF;
    border-right: 1px solid #DFDFDF;
    border-bottom: 1px solid #DFDFDF;
}
.payment__box:first-child {
    border-top: 1px solid #DFDFDF;
}
.payment__body {
    padding: 17px 40px 26px;
    border-left: 1px solid #DFDFDF;
    border-right: 1px solid #DFDFDF;
    border-bottom: 1px solid #DFDFDF;
}
.payment__total-box {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.payment__total {
    font-size: 18px;
    font-weight: 600;
    letter-spacing: 0.05em;
}
.payment__price {
    font-size: 18px;
    font-weight: 600;
    letter-spacing: 0.05em;
}
.payment__instructions {
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 0.05em;
    line-height: calc(19 / 13);
    text-align: center;
    margin-top: 23px;
}

.payment__confirm {
    margin-top: 11px;
    width: 380px;
    max-width: 100%;
    margin-left: auto;
    margin-right: auto;
}
.payment__confirm button {
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 0.15em;
    color: #fff;
    background-color: #000;
    width: 100%;
    height: 50px;
    line-height: 50px;
}
.payment__explain {
    color: #B94A48;
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 0.05em;
    line-height: calc(19 / 13);
    margin-top: 12px;
}
.payment__explain span {
    font-weight: 600;
}
.payment__explain a {
    display: inline-block;
    text-decoration: underline;
}
.payment__info-wrapper {
    border: 1px solid #DFDFDF;
    margin-top: 20px;
}
.payment__info {
    padding: 20px;
}
.payment__info:not(:first-child) {
    border-top: 1px solid #DFDFDF;
}

@media screen and (min-width: 695px) and (max-width: 801px) {
    .payment__box {
        padding: 18px 20px 19px;
    }
    .payment__body {
        padding: 17px 20px 19px;
    }
    .payment__total {
        font-size: 16px;
    }
    .payment__price {
        font-size: 16px;
    }
}

@media screen and (max-width: 694px) {
    .payment__content {
        border: none;
    }
    .payment__box {
        padding: 15px 0 21px;
        border-left: none;
        border-right: none;
    }
    .payment__box:first-child {
        border-top: 1px solid #D9D9D9;
    }
    .payment__dt {
        font-size: 12px;
    }
    .payment__dd {
        font-size: 12px;
    }
    .payment__row:not(:first-child) {
        margin-top: 5px;
    }
    .payment__meta {
        display: flex;
        flex-wrap: wrap;
        flex-direction: column-reverse;
    }
    .payment__body {
        width: 100%;
        padding: 30px 0 25px;
        border-left: none;
        border-right: none;
        border-bottom: none;
    }
    .payment__info-wrapper {
        border-left: none;
        border-right: none;
        width: 100%;
    }
    .payment__info {
        padding-left: 0;
        padding-right: 0;
    }
    .payment__instructions {
        font-size: 11px;
        line-height: calc(32 / 22);
        margin-top: 0;
    }
    .payment__explain {
        font-size: 12px;
        line-height: calc(35 / 24);
        margin-top: 8px;
    }
    .payment__confirm {
        margin-top: 30px;
    }
    .payment__confirm button {
        font-size: 15px;
        letter-spacing: 0;
    }
    .payment__total-box {
        margin-top: 12px;
    }
    .payment__total {
        letter-spacing: 0;
    }
    .payment__price {
        letter-spacing: 0;
    }
}