@use "global" as *;

.to-top {
    position: fixed;
    bottom: 30px;
    right: 30px;
    opacity: 0;
    visibility: hidden;
    transition: all .3s ease 0s;
    z-index: 99;
}
.to-top.is-show {
    opacity: 1;
    visibility: visible;
}
.to-top a {
    display: block;
}
.to-top img {
    width: 34px;
}

@media screen and (max-width: 694px) {
    .to-top {
        right: 14px;
        bottom: 14px;
    }
    .to-top img {
        width: 60px;
    }
}