@use "global" as *;

.new {
    padding-top: 20px;
    border-top: 1px solid #DFDFDF;
}
.new__head {
    font-size: 13px;
    font-weight: 600;
    letter-spacing: 0.05em;
}
.new__dl {
    margin-top: 15px;
}
.new__row {
    display: flex;
    align-items: center;
}
.new__row:not(:first-child) {
    margin-top: 30px;
}
.new__dt {
    font-size: 13px;
    letter-spacing: 0;
    font-weight: 600;
    width: 180px;
    max-width: 100%;
}
.new__dt span {
    color: #B94A48;
}
.new__dd {
    margin-left: 20px;
}
.new__dd-name-wrapper {

}
.new__dd-name {
    position: relative;
}
.new__dd-name label {
    font-size: 13px;
    font-weight: 300;
    position: absolute;
    top: 6.5px;
    left: -18px;
}
.new__dd-name:last-child {
    margin-top: 10px;
}
.new__dd-name [type=text] {
    width: 190px;
    height: 32px;
    background-color: #fff;
    border: 1px solid #707070;
    padding-left: 8px;
}
.new__dd-name [type=text]::placeholder {
    font-size: 12px;
    font-weight: 300;
    letter-spacing: 0;
    color: #878787;
}
.new__dd-address {
    display: flex;
    align-items: center;
}
.new__dd-address li:first-child {
    font-size: 13px;
    font-weight: 300;
}
.new__dd-address li:nth-child(2) {
    width: 95px;
    margin-left: 12px;
}
.new__dd-address li:nth-child(2) [type=text] {
    width: 100%;
    height: 32px;
    background-color: #fff;
    border: 1px solid #707070;
    appearance: none;
    box-shadow: none;
    outline: none;
    padding-left: 8px;
    font-size: 12px;
    font-weight: 300;
}
.new__dd-address li:nth-child(3) {
    font-size: 13px;
    font-weight: 300;
    margin-left: 12px;
}
.new__dd-address li:nth-child(4) {
    width: 95px;
    margin-left: 12px;
}
.new__dd-address li:nth-child(4) [type=text] {
    width: 100%;
    height: 32px;
    background-color: #fff;
    border: 1px solid #707070;
    appearance: none;
    box-shadow: none;
    outline: none;
    padding-left: 8px;
    font-size: 12px;
    font-weight: 300;
}
.new__dd-address li:last-child {
    margin-left: 12px;
    width: 136px;
}
.new__dd-address li:last-child [type=submit] {
    width: 100%;
    height: 32px;
    background-color: #CDCDCD;
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 0.15em;
}
.new__dd-select {
    position: relative;
    width: 192px;
}
.new__dd-select::after {
    content: "";
    position: absolute;
    width: 24px;
    height: 24px;
    background: url(../images/common/select-arrow.png) no-repeat center center / contain;
    top: 4px;
    right: 4px;
    z-index: -1;
}
.new__dd-select select {
    width: 100%;
    height: 32px;
    background-color: transparent;
    border: 1px solid #707070;
    font-size: 12px;
    font-weight: 300;
    color: #878787;
    padding-left: 8px;
}
.new__dd-select select.is-select {
    color: #000 !important;
}
.new__dd-explain {
    font-size: 11px;
    font-weight: 300;
    margin-top: 3px;
    color: #878787;
}
.new__dd-explain.is-red {
    color: #B94A48;
}
.new__register {
    margin-top: 30px;
}
.new__register button {
    width: 380px;
    max-width: 100%;
    height: 50px;
    background-color: #fff;
    border: 1px solid #000;
    text-align: center;
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 0.15em;
}

@media screen and (min-width: 695px) and (max-width: 1149px) {
    .new__row {
        display: block;
    }
    .new__dt {
        width: 100%;
    }
    .new__dd {
        margin-top: 4px;
        margin-left: 0;
    }
    .new__dd-address li:nth-child(2) {
        margin-left: 6px;
    }
    .new__dd-address li:nth-child(3) {
        margin-left: 6px;
    }
    .new__dd-address li:nth-child(4) {
        margin-left: 6px;
    }
    .new__dd-address li:last-child {
        margin-left: 6px;
        width: 90px;
    }
}

@media screen and (max-width: 694px) {
    .new__head {
        font-size: 14px;
        font-weight: 300;
    }
    .new__row {
        display: block;
    }
    .new__dt {
        font-size: 14px;
        font-weight: 300;
        letter-spacing: 0;
        width: 100vw;
        max-width: initial;
        margin-left: calc(50% - 50vw);
        margin-right: calc(50% - 50vw);
        padding-left: calc(50vw - 50%);
        padding-right: calc(50vw - 50%);
        padding-top: 7px;
        padding-bottom: 7px;
        background-color: #F0F0F0;
    }
    .new__dd {
        margin-left: 0;
        margin-top: 10px;
    }
    .new__dd-name-wrapper {
        display: flex;
    }
    .new__dd-name {
        padding-left: 24px;
    }
    .new__dd-name:last-child {
        margin-top: 0;
        margin-left: 10px;
    }
    .new__dd-name {
        width: calc((169 / 347) * 100%);
    }
    .new__dd-name [type=text] {
        width: 100%;
        height: 40px;
    }
    .new__dd-name label {
        font-size: 14px;
        font-weight: 500;
        left: 0;
        top: 10px;
    }
    .new__dd-address li:nth-child(2) {
        width: 80px;
    }
    .new__dd-address li:nth-child(2) [type=text] {
        height: 40px;
    }
    .new__dd-address li:nth-child(4) {
        width: 80px;
    }
    .new__dd-address li:nth-child(4) [type=text] {
        height: 40px;
    }
    .new__dd-address li:last-child {
        width: 130px;
    }
    .new__dd-address li:last-child [type=submit] {
        height: 40px;
        font-size: 11px;
        letter-spacing: 0;
    }
    .new__dd-select {
        width: 100%;
    }
    .new__dd-select select {
        height: 40px;
    }
    .new__dd-select::after {
        top: 10px;
        right: 10px;
    }
    .new__register button {
        font-size: 15px;
        letter-spacing: 0;
    }
    .new__dd-explain {
        font-size: 12px;
    }
    .new__row:not(:first-child) {
        margin-top: 25px;
    }
    .new__register {
        text-align: center;
        margin-top: 20px;
    }
}