@use "global" as *;

.step {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}
.step::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 1px;
    left: 0;
    background-color: #CDCDCD;
    top: 16px;
}
.step__item {
    font-size: 13px;
    font-weight: 300;
    width: 200px;
    max-width: 100%;
    text-align: center;
    z-index: 1;
}
.step__item:nth-child(2) {
    margin-left: 20px;
    margin-right: 20px;
}
.step__number {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: #fff;
    border: 1px solid #CDCDCD;
    color: #CDCDCD;
    text-align: center;
    line-height: 28px;
    margin-left: auto;
    margin-right: auto;
}
.step__number.is-active {
    background-color: #000;
    color: #fff;
    border: 2px solid #000;
}
.step__item span {
    letter-spacing: 0.05em;
    margin-top: 7px;
    display: block;
}

@media screen and (max-width: 694px) {
    .step {
        justify-content: space-around;
        align-items: initial;
    }
    .step__item {
        font-size: 10px;
        letter-spacing: 0.05em;
    }
    /*
    .step__item:first-child {
        width: 41px;
        max-width: 100%;
    }
    .step__item:nth-child(2) {
        width: 92px;
        max-width: 100%;
        margin-left: 52px;
        margin-right: 37px;
    }
    .step__item:nth-child(3) {
        width: 73px;
        max-width: 100%;
    }
    */
    .step__number {
        width: 25px;
        height: 25px;
        line-height: 24px;
        font-size: 12px;
    }
    .step__item span {
        margin-top: 7px;
    }
    .step::after {
        top: 13px;
    }
}