@use "global" as *;

.form-radio {
    width: 13px;
    height: 13px;
    border: 1px solid #000;
    border-radius: 3px;
    background-color: #fff;
    appearance: none;
    box-shadow: none;
    outline: none;
    display: block !important;
    margin: 0;
}
.form-radio:checked {
    background-color: #000;
}

@media screen and (max-width: 694px) {
    .form-radio {
        width: 15px;
        height: 15px;
    }
}