@use "global" as *;

.recommend {

}
.recommend__inner {
    position: relative;
}
.recommend__title {
    font-size: 18px;
    font-weight: 300;
    text-align: center;
    margin-bottom: 37px;
}
.js-recommend-swiper .swiper-slide {
    width: 295px;
    max-width: 100%;
}
.js-recommend-swiper {
    overflow: hidden;
    position: relative;
}
.js-item-swiper .swiper-slide {
    width: 295px;
    max-width: 100%;
}
.js-item-swiper {
    overflow: hidden;
    position: relative;
}
/*
.js-recommend-swiper .swiper-slide, .swiper-wrapper {
    transform: translate3d(0,0,0) !important;
}
*/
.swiper-recommend-next {
    position: absolute;
    width: 48px;
    z-index: 1;
    top: 56%;
    right: 2px;
    transform: translateY(-50%);
    cursor: pointer;
}
.swiper-item-next {
    position: absolute;
    width: 48px;
    z-index: 1;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
    cursor: pointer;
}

@media screen and (min-width: 750px) and (max-width: 1200px) {
    .swiper-recommend-next {
        right: -35px;
    }
}

@media screen and (max-width: 694px) {
    .swiper-recommend-next {
        display: none;
    }
    .swiper-item-next {
        display: none;
    }
    .recommend__title {
        margin-bottom: 30px;
        text-align: center;
        font-size: 15px;
        letter-spacing: 0;
    }
    .recommend__inner {
        padding-right: 0;
    }
    .recommend__link {
        margin-top: 32px;
        padding-right: 14px;
    }
}