@use "global" as *;

.register-form {
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 0.15em;
    background-color: #CDCDCD;
    width: 100%;
    height: 50px;
    line-height: 50px;
    display: block;
    text-align: center;
}
.register-form--black {
    color: #fff;
    background-color: #000;
}

@media screen and (max-width: 694px) {
    .register-form {
        font-size: 15px;
        letter-spacing: 0;
    }
}